export default () => {
    const navItems = document.querySelectorAll('.js-nav-item')

    const goToRoute = (route: string) => {
        const dest = document.querySelector(route)
        if(dest){
            dest.scrollIntoView({ behavior: 'smooth', inline: "nearest"})
        }
        navItems.forEach(item => {
            const selected = item.getAttribute('href') === route
            item.classList[selected ? 'add' : 'remove']('selected')
        })
    }

    const { hash } = window.location
    if(hash)
        goToRoute(hash)

    navItems.forEach(item => {
        const href = item.getAttribute('href')
        if(!href)
            return
        item.addEventListener('click', (e: Event) => {
            e.preventDefault()
            item.classList.add('selected')
            window.location.hash = href
            goToRoute(href)
        })
    })
}